                </div>
                <div class="u-item">
                    <svg xmlns="http://www.w3.org/2000/svg" width="14.667" height="13.574" viewBox="0 0 14.667 13.574" fill="none" v-bind:svg-inline="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill-rule="evenodd" fill="#7A7A7A" d="M11.887 13.574H.667A.667.667 0 010 12.908V6.24c0-.368.298-.667.667-.667h2.321c.217 0 .42-.105.545-.282L7.168.142A.333.333 0 017.59.034L8.8.64a1.667 1.667 0 01.87 1.902l-.602 2.366h4.266c.737 0 1.334.597 1.334 1.333v1.403c0 .174-.034.347-.1.508l-2.064 5.01a.667.667 0 01-.616.412zM4 6.634c.242-.144.455-.337.622-.573l3.14-4.448.44.22c.141.07.214.229.175.38L7.775 4.58a1.333 1.333 0 001.292 1.662h4.266v1.403L11.44 12.24H4V6.633zM1.333 12.24V6.908h1.334v5.333H1.333z"/></svg>
                    {{ data.agree_count || 0 }}
                </div>
                <div
                    @click="onCategoryChange(data.category)"
                    :class="`m-topic-category`"
                    :style="`background-color: ${styles.hoverColor};color:${styles.color};`"
                >
                    <img
                        v-svg-inline
                        class="u-icon"
                        :src="require(`@/assets/img/community/category/${styles.icon}.svg`)"
                    />
                    <div>{{ data.category }}</div>
                </div>
                <svg xmlns="http://www.w3.org/2000/svg" width="272.114" height="27" viewBox="0 0 272.114 27" fill="none" v-bind:svg-inline="''" v-bind:class="'u-mocha'" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><path fill="#FFF" d="M4.334 26.5h267.28v-16c0-5.523-4.576-10-10.099-10H93.264c-42.018 0-47.452 26-88.93 26z"/><path fill="rgba(236, 236, 236, 1)" d="M93.264 0H261.515c5.799 0 10.599 4.701 10.599 10.5V27h-.5.5H.5v-1h3.834q7.342 0 13.883-1.103 5.824-.983 11.37-2.908 4.627-1.606 9.723-4.12 2.042-1.007 4.511-2.33 1.484-.796 4.42-2.414 2.961-1.632 4.462-2.436 2.505-1.343 4.584-2.367 5.206-2.565 9.956-4.208Q72.95 2.139 78.956 1.13 85.696 0 93.264 0v1q-7.485 0-14.142 1.117-5.925.994-11.552 2.942-4.691 1.623-9.841 4.16-2.064 1.017-4.553 2.351-1.496.802-4.453 2.43-2.94 1.621-4.43 2.42-2.483 1.331-4.54 2.346-5.153 2.54-9.838 4.167-5.625 1.953-11.532 2.95Q11.76 27 4.333 27H.5c-.667 0-.667-1 0-1h271.114v.5-.5.5h-.5v-16c0-5.247-4.352-9.5-9.599-9.5H93.264V0z"/></svg>
            </div>
        </div>
        <div
            class="u-box m-topic-box__content"
            :class="[{ withSkin: skin.background }, skin.style]"
            :style="{
                backgroundImage: `url(${skin.background})`,
            }"
        >
            <div class="u-content-aside">
                <a class="m-topic-title" :href="getPostUrl(data.id)" target="_blank" :style="hightStyle">
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="isTop" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FF6969"/><path d="M5.654 10.916l7.671 7.94s4.819-9.49 7.194-9.003c2.376.488-6.481-9.125-5.852-6.28.63 2.845-9.013 7.343-9.013 7.343z" fill="#FFF"/><path d="M4.114 9.325L14.852 20.44l-1.055-5.26-4.602-4.763-5.081-1.092zM3.09 20.808l7.06-5.225L8.137 13.5l-5.048 7.309z" fill="#FFF"/></svg>
                    <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="data.is_star" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FFBB69"/><path d="M21 12l-6.582-2.418L12 3 9.582 9.582 3 12l6.582 2.418L12 21l2.418-6.582L21 12z" fill="#FFF"/></svg>
                    <span>{{ data.title }}</span>
                </a>
                <a class="u-cover" :href="getPostUrl(data.id)" target="_blank">
                    <el-image :src="getBanner(data)" alt="banner" srcset="" />
                </a>

                <div v-if="data.color_tag && data.color_tag.length" class="m-topic-tag">
                    <span v-for="(item, index) in data.color_tag" :key="index" :style="{ backgroundColor: item.color }">
                        {{ item.label }}
                    </span>
                </div>
                <div
                    class="m-topic-collection"
                    v-if="data.collection_id && data.collection.id && data.collection.title"
                >
                    <a :href="`/collection/${data.collection_id}`" target="_blank">
                        <span class="u-label">小册</span>
                        <span class="u-value">{{ data.collection.title }}</span>
                    </a>
                </div>

                <div class="m-topic-userInfo">
                    <a :href="authorLink(data.ext_user_info.id)" target="_blank">
                        <img class="m-topic-userInfo__avatar" :src="showAvatar()" alt="" srcset="" />
                        <span class="m-topic-userInfo__name">{{ data.ext_user_info.display_name }}</span>
                    </a>
                </div>
            </div>

            <a class="u-box-content" :href="getPostUrl(data.id)" target="_blank">
                <a :href="getPostUrl(data.id)" class="m-topic-content" target="_blank">
                    <span class="m-topic-title" target="_blank" :style="hightStyle">
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="isTop" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FF6969"/><path d="M5.654 10.916l7.671 7.94s4.819-9.49 7.194-9.003c2.376.488-6.481-9.125-5.852-6.28.63 2.845-9.013 7.343-9.013 7.343z" fill="#FFF"/><path d="M4.114 9.325L14.852 20.44l-1.055-5.26-4.602-4.763-5.081-1.092zM3.09 20.808l7.06-5.225L8.137 13.5l-5.048 7.309z" fill="#FFF"/></svg>
                        <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" v-bind:svg-inline="''" v-if="data.is_star" v-bind:srcset="''" v-bind:role="'presentation'" v-bind:focusable="'false'" v-bind:tabindex="'-1'"><rect width="24" height="24" rx="4" fill="#FFBB69"/><path d="M21 12l-6.582-2.418L12 3 9.582 9.582 3 12l6.582 2.418L12 21l2.418-6.582L21 12z" fill="#FFF"/></svg>
                        <span>{{ data.title }}</span>
                    </span>
                    <div class="u-introduction" v-html="introduction"></div>
                </a>
                <div v-if="data.extra_images && data.extra_images.length" class="m-topic-imgs scrollbar">
                    <a class="u-item" v-for="(item, index) in data.extra_images" :key="index">
                        <el-image :src="getSquareBanner(item)" fit="fill" />
                    </a>
                </div>
            </a>
        </div>
    </div>
</template>

<script>
import { showAvatar, authorLink, getThumbnail } from "@jx3box/jx3box-common/js/utils";
import { __ossMirror, __cdn } from "@jx3box/jx3box-common/data/jx3box";
import { random } from "lodash";
import { getTimeAgo } from "@/utils/dateFormat";
import { getSkinJson } from "@/service/community";
const skinKey = "community_topic_skin";

export default {
    props: ["data"],
    inject: ["getCategoryStyle", "onCategoryChange"],
    data() {
        return {
            skinJson: {},
        };
    },
    computed: {
        // 卡片皮肤
        skin() {
            if (this.data.decoration_id && this.data.decoration.val) {
                const skinJson = this.skinJson;
                const val = this.data.decoration.val;
                if (skinJson[val]) {
                    return {
                        background: __cdn + `design/decoration/palu/${val}.png`,
                        titleColor: skinJson[val].titleColor,
                        titleHoverColor: skinJson[val].titleHoverColor,
                        borderHoverColor: skinJson[val].borderHoverColor,
                        style: skinJson[val].style || "",
                    };
                }
            }
            // 默认值 未设置返回默认值 实装要判断
            return {
                titleColor: "#0366d6",
                titleHoverColor: "rgba(255, 64, 128, 1)",
                borderHoverColor: "#0366d6",
                style: "",
            };
        },
        hightStyle: function () {
            if (this.data.is_hight && this.data.hight_color) {
                return {
                    color: this.data.hight_color,
                };
            } else {
                return {};
            }
        },
        styles: function () {
            return this.getCategoryStyle(this.data.category || "");
        },
        introduction: function () {
            const data = this.data;
            let introduction = data.introduction;
            // 去掉信封样式的css代码
            introduction = introduction.replace(/\.letter--[\s\S]*$/, "");
            if (introduction) {
                if (introduction.length >= 200) {
                    return introduction + "...";
                } else if (introduction.length <= 0) {
                    return "......";
                } else {
                    return introduction;
                }
            } else {
                return "";
            }
        },
        isTop: function () {
            return this.data.is_top || this.data.is_category_top;
        },
    },
    mounted() {
        this.getSkinJson();
    },
    methods: {
        getSkinJson() {
            const skinJson = sessionStorage.getItem(skinKey);
            if (skinJson) {
                this.skinJson = JSON.parse(skinJson);
            } else {
                getSkinJson().then((res) => {
                    this.skinJson = res.data;
                    sessionStorage.setItem(skinKey, JSON.stringify(res.data));
                });
            }
        },
        getTimeAgo,
        authorLink,
        getBanner: function (data) {
            if (data.banner_img) {
                if (data.banner_img.indexOf("jx3box.com") >= 0) {
                    return getThumbnail(data.banner_img, [168 * 2, 88 * 2]);
                } else {
                    return data.banner_img;
                }
            } else if (data.extra_images && data.extra_images.length) {
                if (data.extra_images[0].indexOf("jx3box.com") >= 0) {
                    return getThumbnail(data.extra_images[0], [168 * 2, 88 * 2]);
                } else {
                    return data.extra_images[0];
                }
            } else {
                // 从1-39中随机选一个
                const randomNum = random(1, 39);
                return __cdn + `design/random_cover/${randomNum}.jpg`;
            }
        },
        getSquareBanner: function (val) {
            if (val.indexOf("jx3box.com") >= 0) {
                return getThumbnail(val, 48 * 2);
            }
            return val;
        },
        showAvatar: function () {
            return showAvatar(this.data?.ext_user_info?.avatar);
        },
        getPostUrl(id) {
            return `/community/${id}`;
        },
    },
};
</script>
